.nft-jacker.jacked {
    .shuffle-btn {
        display: flex;
        position: absolute;
        left: 0px;
        right: 0px;
        margin-left: auto;
        margin-right: auto;
        width: 45px;
        border-radius: 10px;
        text-align: center;
        background-color: black;
        color: white;
        cursor: pointer;
        bottom: 10%;
        z-index: 1000;
        padding: 10px;

        img {
            width: 23px;
        }
    }
}

.nft-jacker {
    .shuffle-btn {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .nft-jacker.jacked {
        .shuffle-btn {
            bottom: 10px;
            left: 90px;
            margin-left: unset;
            margin-right: unset;
        }
    }
}
